<template>
  <div class="shadow p-20">
    <div class="px-4 py-5 bg-gray-200 dark:bg-gray-900 space-y-6 rounded">
      <div class="ml-10 grid grid-cols-1">
        <div class="flex items-center mb-4">
          <input
            type="checkbox"
            v-model="selectedDatas.image"
            id="image"
            class="w-4 h-4 text-blue-600 bg-gray-100 rounded border-gray-300 focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600"
          />
          <label
            class="ml-2 text-xl font-medium text-gray-900 dark:text-gray-300"
            >Image Ipfs Url</label
          >
        </div>
        <div class="flex items-center mb-4">
          <input
            type="checkbox"
            v-model="selectedDatas.animation_url"
            id="animation_url"
            class="w-4 h-4 text-blue-600 bg-gray-100 rounded border-gray-300 focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600"
          />
          <label
            class="ml-2 text-xl font-medium text-gray-900 dark:text-gray-300"
            >Animation Url</label
          >
        </div>
        <div class="flex items-center mb-4">
          <input
            type="checkbox"
            v-model="selectedDatas.external_url"
            class="w-4 h-4 text-blue-600 bg-gray-100 rounded border-gray-300 focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600"
          />
          <label
            class="ml-2 text-xl font-medium text-gray-900 dark:text-gray-300"
            >External Url</label
          >
        </div>
        <div class="flex items-center mb-4">
          <input
            type="checkbox"
            v-model="selectedDatas.youtube_url"
            class="w-4 h-4 text-blue-600 bg-gray-100 rounded border-gray-300 focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600"
          />
          <label
            class="ml-2 text-xl font-medium text-gray-900 dark:text-gray-300"
            >Youtube Url</label
          >
        </div>
        <div class="flex items-center mb-4">
          <input
            type="checkbox"
            v-model="selectedDatas.attributes"
            class="w-4 h-4 text-blue-600 bg-gray-100 rounded border-gray-300 focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600"
          />
          <label
            class="ml-2 text-xl font-medium text-gray-900 dark:text-gray-300"
            >Attributes</label
          >
        </div>
      </div>
      <h1 class="dark:text-white text-xl">{{ jsonData }}</h1>
      <div class="flex justify-center space-x-3">
        <label class="text-sm font-medium text-black dark:text-white">
          Start Number
        </label>
        <input
          type="number"
          v-model="startNum"
          class="border-2 border-gray-300 w-1/4 rounded"
          placeholder="start number..."
        />
        <label class="text-sm font-medium text-black dark:text-white">
          End Number
        </label>
        <input
          type="number"
          v-model="lastNum"
          class="border-2 w-1/4 rounded border-gray-300"
          placeholder="last number..."
        />
      </div>

      <div class="grid grid-cols-1 space-y-3">
        <div>
          <label class="text-sm font-medium text-black dark:text-white">
            Name
          </label>
          <div class="mt-1 flex rounded-md shadow-sm">
            <input
              type="text"
              v-model="jsonData.name"
              class="border-2 w-full rounded border-gray-300"
              placeholder="name"
            />
          </div>
        </div>
        <div>
          <label class="text-sm font-medium text-black dark:text-white">
            Description
          </label>
          <div class="mt-1">
            <textarea
              rows="3"
              v-model="jsonData.description"
              class="mt-1 w-full border-2 border-gray-300 rounded"
              placeholder="description"
            ></textarea>
          </div>
        </div>
      </div>
      <div v-if="selectedDatas.image">
        <label class="text-sm font-medium text-black dark:text-white">
          Image Ipfs Url
        </label>
        <div class="mt-3 flex rounded-md shadow-sm">
          <input
            type="text"
            v-model="jsonData.image"
            class="border-2 w-full rounded border-gray-300"
            placeholder="ipfs://erivfaweJFRAWAFRrtwbg/#png..."
          />
        </div>
      </div>
      <div v-if="selectedDatas.animation_url">
        <label class="text-sm font-medium text-black dark:text-white">
          Animation Url
        </label>
        <div class="mt-3 flex rounded-md shadow-sm">
          <input
            type="text"
            v-model="jsonData.animation_url"
            class="border-2 w-full rounded border-gray-300"
            placeholder="https://ipfs.infura.io/ergftbferawrgbtsarerfgbt/#gltf..."
          />
        </div>
      </div>
      <div v-if="selectedDatas.external_url">
        <label class="text-sm font-medium text-black dark:text-white">
          External Url
        </label>
        <div class="mt-1 flex rounded-md shadow-sm">
          <input
            type="text"
            v-model="jsonData.external_url"
            class="border-2 w-full rounded border-gray-300"
            placeholder="www.example.com..."
          />
        </div>
      </div>
      <div v-if="selectedDatas.youtube_url">
        <label class="text-sm font-medium text-black dark:text-white">
          Youtube Url
        </label>
        <div class="mt-1 flex rounded-md shadow-sm">
          <input
            type="text"
            v-model="jsonData.youtube_url"
            class="border-2 w-full rounded border-gray-300"
            placeholder="www.youtube.com..."
          />
        </div>
      </div>
      <div v-if="selectedDatas.attributes">
        <label class="text-sm font-medium text-black dark:text-white">
          Attributes
        </label>
        <div v-for="i in _attribute" :key="i" class="grid grid-cols-4 gap-4">
          <label class="text-sm font-medium text-black dark:text-white">
            trait_type
          </label>
          <div class="mb-6">
            <input
              type="text"
              class="form-control w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none"
              placeholder="trait_type"
              v-model="i.trait_type"
            />
          </div>
          <label class="text-sm font-medium text-black dark:text-white">
            value
          </label>
          <div class="mb-6">
            <input
              type="text"
              class="form-control w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none"
              placeholder="value"
              v-model="i.value"
            />
          </div>
        </div>
        <div class="text-right sm:px-6">
          <button
            @click="addAttribute()"
            class="inline-flex justify-center py-2 px-4 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
          >
            Add Attributes
          </button>
        </div>
      </div>
    </div>

    <div class="px-4 py-3 bg-white dark:bg-black text-right sm:px-6">
      <button
        @click="multiSave(jsonData)"
        class="inline-flex justify-center py-2 px-4 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
      >
        Save
      </button>
    </div>
  </div>
</template>

<script>
import { onMounted, ref, reactive, computed } from "@vue/runtime-core";
import { useRoute } from "vue-router";
export default {
  setup() {
    let projectId = ref();
    let selectedDatas = reactive({
      external_url: false,
      youtube_url: false,
      image: false,
      attributes: false,
      animation_url: false,
    });

    let _attribute = reactive([]);
    const addAttribute = () => {
      _attribute.push({ trait_type: "", value: "" });
    };

    onMounted(() => {
      const route = useRoute();
      projectId.value = route.params.projectId;
    });
    ("use strict");

    let jsonData = computed(() => {
      let instance = reactive({
        name: "",
        description: "",
        compiler: "ArtKonnection",
      });
      if (selectedDatas.external_url) {
        instance.external_url = "";
      }
      if (selectedDatas.youtube_url) {
        instance.youtube_url = "";
      }
      if (selectedDatas.image) {
        instance.image = "";
      }
      if (selectedDatas.animation_url) {
        instance.animation_url = "";
      }
      if (selectedDatas.attributes) {
        instance.attributes = _attribute;
      }
      return instance;
    });
    const exportToJsonFile = (_jsonData, _fileName) => {
      _jsonData.date = Date.now();
      let dataStr = JSON.stringify(_jsonData);
      let dataUri =
        "data:application/json;charset=utf-8," + encodeURIComponent(dataStr);

      let exportFileDefaultName = `${_fileName}.json`;

      let linkElement = document.createElement("a");
      linkElement.setAttribute("href", dataUri);
      linkElement.setAttribute("download", exportFileDefaultName);
      linkElement.click();
    };

    let startNum = ref();
    let lastNum = ref();

    const multiSave = (jsonData) => {
      const _jsonData = jsonData;
      let instasceImage;
      let instanceAnimationUrl;
      const instanceName = _jsonData.name;
      if (selectedDatas.image) {
        instasceImage = _jsonData.image.split("#");
      }
      if (selectedDatas.animation_url) {
        instanceAnimationUrl = _jsonData.animation_url.split("#");
      }

      for (let i = startNum.value; i <= lastNum.value; i++) {
        _jsonData.name = `${instanceName} #${i}`;
        _jsonData.date = Date.now();
        _jsonData.edition = i;
        if (selectedDatas.image) {
          _jsonData.image = `ipfs://${instasceImage[0]}/${
            projectId.value * 1000000 + i
          }.${instasceImage[1]}`;
        }
        if (selectedDatas.animation_url) {
          _jsonData.animation_url = `ipfs://${instanceAnimationUrl[0]}/${
            projectId.value * 1000000 + i
          }.${instanceAnimationUrl[1]}`;
        }
        const _fileName = `${projectId.value * 1000000 + i}`;
        exportToJsonFile(_jsonData, _fileName);
      }
    };

    return {
      jsonData,
      exportToJsonFile,
      addAttribute,
      multiSave,
      projectId,
      selectedDatas,
      _attribute,
      startNum,
      lastNum,
    };
  },
};
</script>


