<template>
  <div>
    <div class="bg-transparent">
      <div class="container mx-auto my-5 p-5">
        <div class="md:flex no-wrap">
          <!-- Left Side -->
          <div class="w-full md:w-3/12">
            <!-- Profile Card -->
            <div
              class="bg-transparent rounded p-3 ml-3 mb-3 border-2 h-full border-black dark:border-white"
            >
              <div class="image overflow-hidden">
                <img
                  class="h-auto w-full mx-auto"
                  :src="projectData.url"
                  alt=""
                />
              </div>
              <h1
                class="text-black dark:text-white font-bold text-xl leading-8 my-1"
              >
                {{ projectData.projectName }}
              </h1>
              <h3
                class="text-gray-600 dark:text-gray-100 font-lg text-semibold leading-6"
              >
                By {{ projectData.projectArtistName }}
              </h3>
              <p
                class="text-sm text-gray-500 dark:text-gray-300 hover:text-gray-600 leading-6 overflow-hidden"
              >
                {{ projectData.projectDescription }}
              </p>
              <p
                class="bg-transparent text-gray-600 dark:text-gray-300 py-2 px-3 mt-3 divide-y rounded shadow-sm"
              >
                <span class="flex items-arostretchund py-3">
                  <span class="m-auto">
                    <span>Status</span
                    ><span
                      v-if="projectData.projectActive"
                      class="bg-green-500 ml-3 py-1 px-2 rounded text-white text-sm"
                      >Active</span
                    >
                    <span
                      v-else
                      class="bg-red-500 ml-3 py-1 px-2 rounded text-white text-sm"
                      >Inactive</span
                    ></span
                  >

                  <span class="m-auto"
                    ><span>Whitelist mint</span>
                    <span
                      v-if="projectData.whitelistMintActive"
                      class="bg-green-500 ml-3 py-1 px-2 rounded text-white text-sm"
                      >Active</span
                    >
                    <span
                      v-else
                      class="bg-red-500 ml-3 py-1 px-2 rounded text-white text-sm"
                      >Inactive</span
                    ></span
                  >
                </span>
              </p>
            </div>
            <!-- End of profile card -->
          </div>
          <!-- Right Side -->
          <div class="w-full md:w-9/12 mx-2 h-64 mb-64">
            <!-- Profile tab -->
            <!-- About Section -->
            <div
              class="bg-transparent p-3 border-2 shadow dark:border-white border-black rounded"
            >
              <div
                class="flex justify-end space-x-2 font-semibold text-gray-900 leading-8 mb-5"
              >
                <router-link :to="`/aboutcollection/${projectData.projectId}`">
                  <button
                    class="w-full border-2 rounded-full border-black dark:border-white dark:text-white mr-3 text-center uppercase"
                  >
                    go to collection
                  </button>
                </router-link>
              </div>
              <div class="text-gray-700 dark:text-gray-200">
                <div class="grid md:grid-cols-2 text-sm">
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">License</div>
                    <div class="px-4 py-2">
                      {{ projectData.projectLicense }}
                    </div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">Artist Address</div>
                    <div
                      class="px-4 py-2 overflow-auto scrollbar-hide dark:text-blue-300 text-blue-600"
                    >
                      <a
                        :href="`${KLAYTNSCOPE}/account/${projectData.projectArtistAddress}`"
                        target="_blank"
                        >{{ projectData.projectArtistAddress }}</a
                      >
                    </div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">Minted</div>
                    <div class="px-4 py-2">
                      {{ projectData.projectInvocation - 1 }}
                    </div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">Total supply</div>
                    <div class="px-4 py-2">
                      {{ projectData.projectMaxInvocation }}
                    </div>
                  </div>

                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">Website</div>
                    <div class="px-4 py-2">
                      <a
                        class="text-blue-800 dark:text-blue-300"
                        :href="projectData.projectWebsite"
                        target="_blank"
                        >{{ projectData.projectWebsite }}</a
                      >
                    </div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">
                      Mint limit per block
                    </div>
                    <div class="px-4 py-2">{{ projectData.mintLimit }}</div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">Price</div>
                    <div class="px-4 py-2">{{ projectData.price }} KLAY</div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">White List Price</div>
                    <div class="px-4 py-2">
                      {{ projectData.whitelistPrice }} KLAY
                    </div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">
                      Mint Start Block Number
                    </div>
                    <div class="px-4 py-2">
                      {{ projectData.mintStartBlockNum }}
                    </div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">
                      Current Block Number
                    </div>
                    <div class="px-4 py-2">{{ currentBlockNumber }}</div>
                  </div>
                  <div class="grid grid-cols-2">
                    <div class="px-4 py-2 font-semibold">royalty %</div>
                    <div class="px-4 py-2 overflow-auto scrollbar-hide">
                      {{ projectData.royalty }}
                    </div>
                  </div>
                </div>
                <div class="pl-4 mt-3">
                  <label
                    for="steps-range"
                    class="block mb-2 text-sm font-medium text-black dark:text-white"
                    >Count : {{ num }}<br />
                    Your Balance : {{ balance }}KLAY
                  </label>
                  <input
                    id="steps-range"
                    type="range"
                    min="0"
                    :max="projectData.mintLimit"
                    v-model="num"
                    step="1"
                    class="w-3/4 h-2 appearance-none bg-black dark:bg-white rounded-full accent-pink-500 text-white"
                  />
                </div>
                <div class="flex justify-center space-x-3">
                  <button
                    class="border-2 rounded-full border-black dark:border-white dark:text-white active:bg-green-600 font-bold uppercase text-sm px-6 py-3 shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="button"
                    v-on:click="mint()"
                  >
                    Total Price : {{ num * projectData.price }} <br />
                    Public Mint
                  </button>
                  <button
                    class="border-2 rounded-full border-black dark:border-white dark:text-white active:bg-green-600 font-bold uppercase text-sm px-6 py-3 shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="button"
                    v-on:click="wMint()"
                    v-if="projectData.whitelistMintActive"
                  >
                    Total White List Price :
                    {{ num * projectData.whitelistPrice }} <br />
                    Whitelist Mint
                  </button>
                </div>
              </div>
            </div>
            <!-- End of about section -->

            <div
              class="bg-transparent border-2 dark:border-white border-black p-3 shadow rounded my-4"
            >
              <div class="grid grid-cols-1">
                <div>
                  <div
                    class="flex items-center space-x-2 font-semibold text-black dark:text-white leading-8 mb-3"
                  >
                    <span clas="text-green-500">
                      <svg
                        class="h-5"
                        xmlns="http://www.w3.org/2000/svg"
                        fill="none"
                        viewBox="0 0 24 24"
                        stroke="currentColor"
                      >
                        <path
                          stroke-linecap="round"
                          stroke-linejoin="round"
                          stroke-width="2"
                          d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z"
                        />
                      </svg>
                    </span>
                    <span class="tracking-wide">History</span>
                  </div>
                  <div
                    class="h-64 overflow-auto scrollbar-hide divide-y-4 divide-black dark:divide-white"
                  >
                    <div v-for="i in history" :key="i">
                      <div class="text-black dark:text-white">
                        Token {{ i.returnValues._tokenId }}
                      </div>
                      <div class="text-black dark:text-white mt-2 text-xs">
                        Minted To {{ i.returnValues._to }}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <br /><br /><br /><br /><br />
      <div class="bg-white dark:bg-black h-auto mt-64 xl:mt-24 lg:mt-24">
        <hr class="border-black dark:border-white m-6" />
        <span class="text-black dark:text-white uppercase">minted tokens</span>
        <hr class="border-black dark:border-white m-6" />

        <div
          class="pt-4 grid lg:grid-cols-3 xl:grid-cols-3 grid-cols-2 gap-3 m-3 p-3"
        >
          <div
            v-for="(item, i) in mintedTokens.tokens"
            :key="i"
            class="border-b-2 w-auto border-black p-3 dark:border-white"
          >
            <img
              v-if="!notImage"
              class="w-full justify-self-center"
              :src="`${URL}/assets/${item}`"
              alt="Token image"
              @error="notImage = true"
            />
            <model-viewer
              v-if="notImage"
              class="w-full h-100px"
              :src="`${URL}/assets/${item}`"
              auto-rotate
            ></model-viewer>

            <p class="m-3 text-xl dark:text-white">
              <b># {{ item }}</b>
            </p>
          </div>
        </div>
      </div>
    </div>

    <div v-if="showFail">
      <div
        class="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
      ></div>

      <div class="fixed z-10 inset-0 overflow-y-auto">
        <div
          class="flex items-end sm:items-center justify-center min-h-full p-4 text-center sm:p-0"
        >
          <div
            class="relative bg-white dark:bg-black rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:max-w-lg sm:w-full"
          >
            <div class="bg-white dark:bg-black px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
              <div class="sm:flex sm:items-start">
                <div
                  class="mx-auto flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-red-100 sm:mx-0 sm:h-10 sm:w-10 text-red-500"
                >
                  <i class="fa-solid fa-triangle-exclamation"></i>
                </div>
                <div class="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                  <h3
                    class="text-lg leading-6 font-medium text-black dark:text-white uppercase"
                    id="modal-title"
                  >
                    fail
                  </h3>
                  <div class="mt-2">
                    <p
                      class="text-sm text-gray-500 dark:text-gray-200 overflow-auto"
                    >
                             .
                      :{{ failMsg }}
                    </p>
                  </div>
                </div>
              </div>
            </div>
            <div
              class="bg-white dark:bg-black px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse"
            >
              <button
                type="button"
                @click="showFail = false"
                class="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-red-600 text-base font-medium text-white hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="showSuccess">
      <div
        class="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
      ></div>

      <div class="fixed z-10 inset-0 overflow-y-auto">
        <div
          class="flex items-end sm:items-center justify-center min-h-full p-4 text-center sm:p-0"
        >
          <div
            class="relative bg-white dark:bg-black rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:max-w-lg sm:w-full"
          >
            <div class="bg-white dark:bg-black px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
              <div class="sm:flex sm:items-start">
                <div
                  class="mx-auto flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-green-100 sm:mx-0 sm:h-10 sm:w-10 text-green-500"
                >
                  <i class="fa-solid fa-check"></i>
                </div>
                <div class="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                  <h3
                    class="text-lg leading-6 font-medium text-black dark:text-white uppercase"
                    id="modal-title"
                  >
                    success
                  </h3>
                  <div class="mt-2">
                    <p class="text-sm text-gray-500 dark:text-gray-200">
                      Your request has been successfully executed.
                    </p>
                  </div>
                </div>
              </div>
            </div>
            <div
              class="bg-white dark:bg-black px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse"
            >
              <button
                type="button"
                @click="showSuccess = false"
                class="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-green-600 text-base font-medium text-white hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 sm:ml-3 sm:w-auto sm:text-sm"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, ref } from "@vue/reactivity";
import { useRoute } from "vue-router";
import { onMounted } from "@vue/runtime-core";
import Caver from "caver-js";
import { CONTRACT_ABI, CONTRACT_ADDRESS } from "@/assets/artKonnection.js";

export default {
  setup() {
    let caver = null;
    let contract = null;
    let projectId = ref(0);
    let num = ref(0);
    let showFail = ref(false);
    let showSuccess = ref(false);
    let failMsg = ref("error");
    let notImage = ref(false);
    let mintedTokens = reactive({
      tokens: [],
    });

    let projectData = reactive({
      projectName: "",
      projectDescription: "",
      projectId: 0,
      url: "",
      projectWebsite: "",
      projectInvocation: 0,
      projectMaxInvocation: 0,
      projectArtistName: "",
      projectArtistAddress: "",
      projectLicense: "",
      projectActive: false,
      price: 0,
      whitelistPrice: 0,
      mintLimit: 0,
      mintStartBlockNum: 0,
      whitelistMintActive: false,
    });
    // const binArrayToJson = function (binArray) {
    //   let str = "";
    //   for (let i = 0; i < binArray.length; i++) {
    //     str += String.fromCharCode(parseInt(binArray[i]));
    //   }
    //   return JSON.parse(str);
    // };

    let currentBlockNumber = ref("");

    const _projectDetails = async (_projectId) => {
      await contract.methods
        .projectDetails(_projectId)
        .call()
        .then((res) => {
          projectData.projectName = res.projectName;
          projectData.projectDescription = res.description;
          projectData.projectId = _projectId;
          projectData.projectWebsite = res.website;
          projectData.projectArtistName = res.artist;
          projectData.projectLicense = res.license;
          projectData.url = `${process.env.VUE_APP_SERVER_URL}/coverassets/${res.projectName}`;
        })
        .catch((err) => {
          console.log(err);
        });
    };
    let balance = ref();
    const _getBalance = () => {
      if (window.klaytn.selectedAddress !== undefined) {
        caver.rpc.klay.getBalance(window.klaytn.selectedAddress).then((res) => {
          balance.value = caver.utils
            .fromPeb(caver.utils.hexToNumberString(res), "KLAY")
            .split(".")[0];
        });
      }
    };

    onMounted(() => {
      const route = useRoute();
      projectId.value = Number(route.params.projectid);
      caver = new Caver(
        window.klaytn._kaikas.isEnabled()
          ? window.klaytn
          : process.env.VUE_APP_RPC_URL
      );
      contract = new caver.klay.Contract(CONTRACT_ABI, CONTRACT_ADDRESS);
      _projectDetails(projectId.value);
      _projectTokenInfo(projectId.value);
      _projectShowAllTokens(projectId.value);
      _mintingInformation(projectId.value);
      _getCurrentBlock();
      _getTokenLogs(projectId.value);
      _getBalance();
      _royaltyInfo();
    });

    const _getCurrentBlock = () => {
      setInterval(() => {
        caver.rpc.klay.getBlockNumber().then((res) => {
          const _currentBlockNumber = caver.utils.hexToNumberString(res);
          currentBlockNumber.value = _currentBlockNumber;
        });
      }, 1000);
    };
    const _royaltyInfo = async () => {
      await contract.methods
        .royaltyInfo(1, 100)
        .call()
        .then((res) => {
          projectData.royalty = (100 * res[1]) / 100;
        });
    };

    const _projectTokenInfo = async (_projectId) => {
      await contract.methods
        .projectTokenInfo(_projectId)
        .call()
        .then(async (res) => {
          projectData.projectArtistAddress = res.artistAddress;
          projectData.projectActive = res.active;
          projectData.whitelistMintActive = res.whitelistMintActive;
          projectData.projectInvocation = res.invocations;
          projectData.projectMaxInvocation = res.maxInvocations;
          projectData.whitelistPrice = caver.utils.fromPeb(
            res.whitelistPricePerTokenInPeb,
            "KLAY"
          );
          projectData.price = caver.utils.fromPeb(
            res.pricePerTokenInPeb,
            "KLAY"
          );
        })
        .catch((err) => {
          console.log(err);
        });
    };
    const _projectShowAllTokens = async (_projectId) => {
      await contract.methods
        .projectShowAllTokens(_projectId)
        .call()
        .then(async (res) => {
          mintedTokens.tokens = res;
        })
        .catch((err) => {
          console.log(err);
        });
    };

    const _mintingInformation = async (_projectId) => {
      await contract.methods
        .mintingInformation(_projectId)
        .call()
        .then((res) => {
          projectData.mintLimit = res.mintLimitPerBlock;
          projectData.mintStartBlockNum = res.mintStartBlockNumber;
        })
        .catch((err) => {
          console.log(err);
        });
    };

    const mint = () => {
      const totalvalue = Number(projectData.price) * num.value;
      const peb = caver.utils.toPeb(caver.utils.toBN(totalvalue), "KLAY");
      contract.methods
        .publicMint(Number(num.value), projectId.value)
        .estimateGas({
          from: window.klaytn.selectedAddress,
          value: peb,
        })
        .then(async (gasAmount) => {
          await contract.methods
            .publicMint(Number(num.value), projectId.value)
            .send({
              from: window.klaytn.selectedAddress,
              gas: gasAmount,
              value: peb,
            })
            .then(() => {
              showSuccess.value = true;
            })
            .catch((err) => {
              failMsg.value = err.message;
              showFail.value = true;
            });
        })
        .catch((err) => {
          console.log(err);
        });
    };
    const wMint = () => {
      const totalvalue = Number(projectData.whitelistPrice) * num.value;
      const peb = caver.utils.toPeb(caver.utils.toBN(totalvalue), "KLAY");
      contract.methods
        .whitelistMint(Number(num.value), projectId.value)
        .estimateGas({
          from: window.klaytn.selectedAddress,
          value: peb,
        })
        .then(async (gasAmount) => {
          await contract.methods
            .whitelistMint(Number(num.value), projectId.value)
            .send({
              from: window.klaytn.selectedAddress,
              gas: gasAmount,
              value: peb,
            })
            .then(() => {
              showSuccess.value = true;
            })
            .catch((err) => {
              failMsg.value = err;
              showFail.value = true;
            });
        })
        .catch((err) => {
          console.log(err);
          failMsg.value = err.message;
          showFail.value = true;
        });
    };
    let history = ref();

    const _getTokenLogs = (_projectId) => {
      contract
        .getPastEvents("Mint", {
          filter: { _projectId: [_projectId] }, // Using an array means OR: e.g. 20 or 23
          fromBlock: 90000000,
          toBlock: "latest",
        })
        .then((events) => {
          if (events !== undefined && events.length !== undefined) {
            history.value = events.reverse();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };

    const URL = ref(process.env.VUE_APP_SERVER_URL);
    const KLAYTNSCOPE = ref(process.env.VUE_APP_KLAYTN_SCOPE_URL);
    return {
      projectData,
      balance,
      mint,
      wMint,
      URL,
      KLAYTNSCOPE,
      num,
      mintedTokens,
      showFail,
      showSuccess,
      failMsg,
      notImage,
      currentBlockNumber,
      history,
    };
  },
};
</script>

<style scoped>
@media (min-width: 640px) {
  model-viewer {
    height: 200px;
  }
}
@media (min-width: 768px) {
  model-viewer {
    height: 300px;
  }
}
@media (min-width: 1024px) {
  model-viewer {
    height: 400px;
  }
}
@media (min-width: 1280px) {
  model-viewer {
    height: 600px;
  }
}
</style>
