<template>
  <div>
    <div class="flex items-center space-x-10 justify-center m-3">
      <div>
        <a
          rel="noopener noreferrer"
          href="https://art-konnection.gitbook.io/artkonnection/artist-guide/"
          title="gitbook"
          class="text-black dark:text-white"
          target="_blank"
          ><span class="mr-3"> Artist Guide </span>
          <i class="fa-solid fa-book-bookmark"></i>
        </a>
      </div>
      <div class="flex border-2 rounded">
        <input
          type="text"
          class="px-4 py-2 w-3/4 dark:bg-black dark:text-white"
          placeholder="Search..."
          v-model="search"
        />
        <button class="px-4">
          <svg
            class="w-6 h-6 dark:text-white"
            fill="currentColor"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path
              d="M16.32 14.9l5.39 5.4a1 1 0 0 1-1.42 1.4l-5.38-5.38a8 8 0 1 1 1.41-1.41zM10 16a6 6 0 1 0 0-12 6 6 0 0 0 0 12z"
            />
          </svg>
        </button>
      </div>
    </div>
    <div class="grid grid-cols-2 xl:grid-cols-4 lg:grid-cols-3 justify-around">
      <div v-for="(item, i) in filteredProjects" :key="i">
        <router-link :to="`/forartistsview/${item.projectId}`">
          <div
            class="m-3 overflow-hidden shadow-lg border-l-2 border-r-2 border-black dark:border-white p-3"
          >
            <img class="w-full" :src="item.url" alt="Sunset in the mountains" />
            <div>
              <div
                class="font-bold text-xl m-3 p-3 border-b-2 text-black dark:text-white border-black dark:border-white"
              >
                {{ item.projectName }}
              </div>
              <p
                class="text-gray-700 dark:text-white text-base border-b-2 border-black dark:border-white overflow-hidden m-3 p-3"
              >
                {{ item.projectDescription }}
              </p>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { CONTRACT_ABI, CONTRACT_ADDRESS } from "@/assets/artKonnection.js";
import Caver from "caver-js";
import { onMounted, ref, reactive, computed } from "@vue/runtime-core";
export default {
  setup() {
    let caver;
    let contract;
    let projects = ref();
    let projectDetails = reactive([]);
    onMounted(() => {
      caver = new Caver(window.klaytn);
      contract = new caver.klay.Contract(CONTRACT_ABI, CONTRACT_ADDRESS);
      _getArtistProjectIds();
    });
    let search = ref("");
    const filteredProjects = computed(() => {
      return projectDetails.filter((post) => {
        return post.projectName
          .toLowerCase()
          .includes(search.value.toLowerCase());
      });
    });
    const getDetail = async (_projectId) => {
      await contract.methods
        .projectDetails(_projectId)
        .call()
        .then((res) => {
          const _instance = {
            projectName: res.projectName,
            projectDescription: res.description,
            projectId: _projectId,
            url: `${process.env.VUE_APP_SERVER_URL}/coverassets/${res.projectName}`,
          };
          projectDetails.push(_instance);
        })
        .catch((err) => {
          console.log(err);
        });
    };
    const _getArtistProjectIds = async () => {
      await contract.methods
        .getArtistProjectIds(window.klaytn.selectedAddress)
        .call()
        .then((res) => {
          if (!res.length > 0) {
            window.location.replace(process.env.VUE_APP_CLIENT_URL);
          }
          projects.value = res;
        })
        .catch((err) => {
          console.log(err);
        });
      for (let i = 0; i < projects.value.length; i++) {
        getDetail(projects.value[i]);
      }
    };

    return {
      filteredProjects,
      search,
    };
  },
};
</script>
